import uniLoadMore from '../../components/uni-load-more/uni-load-more.vue'
export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			show: 0,
			data: [],
			status: '',
			more: 'more',
			page: 1,
			loadRecord: true

		}
	},
	methods: {
		returns() {
			uni.switchTab({
				url: '/pages/index/user'
			});
		},
		jump(url) {
			
			uni.navigateTo({
				url: url
			})
		},
		deletes(id,index){ //删除订单
			let that = this
			uni.showModal({
				title: '提示',
				content: '是否确定删除订单？',
				success: function (res) {
					if (res.confirm) {
						that.service.entire(that, 'post', that.APIconfig.api_root.subuser.s_delete, {
							user_id: that.$store.state.user.id,
							id:id
						}, function(self, res) {
							uni.showToast({
								icon:'none',
								title:res.msg
							})
							if(res.code == 0)self.data.splice(index,1)
						})
					} else if (res.cancel) {
						// console.log('用户点击取消');
						return
					}
				}
			});
		
			
		},
		chiose(status) {
			this.more = 'loading'
			this.data = []
			this.show = status
			this.loadRecord = true
			this.service.entire(this, 'post', this.APIconfig.api_root.subuser.s_order, {
				user_id: this.$store.state.user.id,
				is_more: 1,
				status: status,
				page: 1
			}, function(self, res) {
				self.data = res.data.data
				if (res.data.data.length < 10) {
					self.more = 'noMore'
					self.loadRecord = false
				}
			})
		},
		loadMore() {
			let page = this.page
			this.more = 'loading'
			this.service.entire(this, 'post', this.APIconfig.api_root.subuser.s_order, {
				user_id: this.$store.state.user.id,
				is_more: 1,
				status: this.show,
				page: page
			}, function(self, res) {

				let data = self.data

				data.push(...res.data.data)
				self.data = data
				console.log(self.data);
				self.page = page + 1
				self.more = 'more'
				if (res.data.data.length < 10) {
					self.more = 'noMore'
					self.loadRecord = false
					return
				}
			})
		}
	},
	onLoad(options) {
		this.status = options.status
		this.show = options.status
	},
	onReachBottom() {
		if (this.loadRecord == false) return
		this.loadMore()
	},
	onShow() {
		this.data.length = 0
		this.page = 1
		this.loadMore()
	}
}
